<template>
  <div class="result-container">
    <SearchInput></SearchInput>
    <div class="res-content">

      <Tip v-for="item in documents" :key="item.id" :imgsrc="item.document.Url" :textcontent="item.text"></Tip>
    </div>
    <div class="pagination">
      <el-pagination background layout="prev, pager, next" :total="total" :current-page="currentPage" @current-change="currentChange" @prev-click="prevClick" @next-click="nextClick" ref="pagination">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import Tip from '@/views/Result/Tip'
import { mapState } from 'vuex'
export default {
  data() {
    return {
      currentPage: this.page
    }
  },
  methods: {
    currentChange() {
      const targetPage = this.$refs.pagination.internalCurrentPage
      this.$store.dispatch('pageJump', { targetPage, input: this.keyword })
    },
    prevClick() {
      const targetPage = this.page - 1
      this.$store.dispatch('pageJump', { targetPage, input: this.keyword })
    },
    nextClick() {
      const targetPage = this.page + 1
      this.$store.dispatch('pageJump', { targetPage, input: this.keyword })
    }
  },

  computed: {
    ...mapState(['documents', 'pageCount', 'total', 'page', 'keyword'])
  },
  mounted() {
    this.keyword = this.$route.query.input
  },
  components: {
    Tip
  }
}
</script>

<style lang="less" scoped>
.result-container {
  padding-top: 20px;
  .res-content {
    width: 800px;
    margin: 0 auto;
    margin-top: 20px;
  }
  .pagination {
    text-align: center;
  }
}
</style>