import Vue from 'vue'
import Vuex from 'vuex'
import { reqResultList } from '@/api/index'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    keyword: '',
    documents: [],
    limit: 0,
    page: 0,
    total: 0,
    pageCount: 0,
    time: 0,
  },
  getters: {},
  mutations: {
    SETINPUT(state, value) {
      state.keyword = value
    },
    GETRESULT(state, value) {
      state.documents = value.documents
      state.limit = value.limit
      state.page = value.page
      state.total = value.total
      state.pageCount = value.pageCount
      state.time = value.time
    },
  },
  actions: {
    async getResult(content, value) {
      const res = await reqResultList(value)
      if ((res.state = 'true')) {
        content.commit('GETRESULT', res.data)
      }
    },
    async pageJump(content, value) {
      const res = await reqResultList(value.input, value.targetPage)
      if ((res.state = 'true')) {
        content.commit('GETRESULT', res.data)
      }
    },
  },
  modules: {},
})
